.manual{width:1080px;height:calc(100vh - 68px);position:fixed;left:calc(50% - 540px);bottom:0;overflow:scroll;scrollbar-width:thin;background-color:#ececec;z-index:501}@media all and (max-width:1090px){.manual{width:calc(100vw - 10px);left:5px}}.manual section{padding:14px 40px}.manual section:last-child{padding-bottom:80px}.manual p{margin-top:8px;font-size:18px;line-height:1.6em;overflow:hidden}.manual ul{margin-left:22px;font-size:18px;line-height:1.6em}.manual h1{font-size:30px;font-weight:700;padding:40px 10px 20px 10px;text-align:center}.manual h2{background-color:#ececec;position:sticky;top:0;padding:30px;font-size:22px;text-decoration:underline}.manual .imgFloat{position:relative;float:right;width:250px;height:250px;margin:0 0 24px 24px;z-index:-1}@media all and (max-width:620px){.manual .imgFloat{float:none;width:auto;height:auto;margin:24px}}.manual .select{color:#ed8e33;border-radius:3px;background-color:#fafafa;padding:2px 6px;white-space:nowrap}.manual .selectRed{white-space:nowrap;text-decoration:underline;color:#f00}.line{width:100%;margin:4px auto;border-radius:5px;height:2px;background-color:#dcdcdc}.imgFull{width:100%;margin-top:10px}